import React from 'react'
import './Bosses.css'
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

function Bosses() {

    const [state, setState] = React.useState({
        checked: false,
      });
    
    const handleChange = (event) => {
        setState({checked: true});
    };

    console.log(state.checked);

    return (
        <>
        <div className='main-bosses'>
            <img className='background-boss' src='images/bosses/bosses_background.jpg' alt='Bosses' />
            <div className='section-bosses'>
                <div className='content-bosses'>
                    <div className="fade_line_oc-bosses">
                        <img className='line-oc-bosses' src='images/logo_line_white.png' alt='Line' />
                    </div>
                    <div className='items-bosses'>
                        <h1>BOSSES</h1>
                    </div>
                    <div className="fade_line_oc-bosses-2">
                        <img className='line-oc-bosses-2' src='images/logo_line_white.png' alt='Line' />
                    </div>
                </div>
                <div className='section-timer'>
                    <h1>SIGUIENTE BOSS EN:</h1>
                    <h1>00:00:00</h1>
                </div>
                <div className='container_bosses_info'>
                    <div className='container_block_bosses'>
                        <h2 className='boss_left'>Garmoth</h2>
                        <img className='img_boss' src='images/bosses/garmoth.jpg' alt='Boss' />
                        <h2 className='boss_right'>Karanda</h2>
                        <img className='img_boss' src='images/bosses/karanda.jpg' alt='Boss' />
                    </div>
                </div>
                <div className='drops_bosses'>
                    <h1>DROPS</h1>
                    <div className="line_drops">
                        <img className='line_drops_bosses' src='images/logo_line_white.png' alt='Line' />
                    </div>
                    <div className='img_rombo'>
                        <div className='drop_imgs_left'>
                            <Popup
                                trigger={open => (
                                <img id='img_drop_1' className='rombo' src='images/bosses/corazon_garmoth.png' alt='Drop' />
                                )}
                                position="bottom center"
                                on={['hover', 'focus']}
                                closeOnDocumentClick
                            >
                                <div className='drop_info'>
                                    <h3>Corazon de Garmoth</h3>
                                    <img src='images/bosses/plata.png' alt='Plata' />
                                    <span>6.800.000.000</span>
                                </div>
                            </Popup>
                            <img className='rombo' src='images/rombo.png' alt='Rombo' />
                        </div>
                        <div className='drop_imgs_right'>
                            <Popup
                                trigger={open => (
                                <img id='img_drop_2' className='rombo' src='images/bosses/corazon_karanda.png' alt='Drop' />
                                )}
                                position="bottom center"
                                on={['hover', 'focus']}
                                closeOnDocumentClick
                            >
                                <div className='drop_info'>
                                    <h3>Corazon de Karanda</h3>
                                    <img src='images/bosses/plata.png' alt='Plata' />
                                    <span>6.000.000.000</span>
                                </div>
                            </Popup>
                            <img className='rombo' src='images/rombo.png' alt='Rombo' />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div className='main-bosses'>
            <img className='background-boss' src='images/bosses/bosses_background_2.jpeg' alt='Bosses' />
            <div className='section-bosses-2'>
            <div className="line_drops">
                <img className='line_drops_bosses' src='images/logo_line_white.png' alt='Line' />
            </div>
            <h1 className='title_map'>MAPA DE BOSSES</h1>
            <div className="line_drops">
                <img className='line_drops_bosses' src='images/logo_line_white.png' alt='Line' />
            </div>
            <map name="image-map" id='image-map'>
                <Popup trigger={<area target="" alt="Muraka" title="Muraka" coords="394,592,39" shape="circle"/>} modal>
                    <h1>Muraka</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Offin" title="Offin" coords="405,748,43" shape="circle"/>} modal>
                    <h1>Offin</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Garmoth" title="Garmoth" coords="759,765,45" shape="circle"/>} modal>
                    <h1>Garmoth</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Quint" title="Quint" coords="469,485,42" shape="circle"/>} modal>
                    <h1>Quint</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Karanda" title="Karanda" coords="630,463,39" shape="circle"/>} modal>
                    <h1>Karanda</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Dim Tree Spirit" title="Dim Tree Spirit" coords="713,485,40" shape="circle"/>} modal>
                    <h1>Dim Tree Spirit</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Kzarka" title="Kzarka" coords="808,643,39" shape="circle"/>} modal>
                    <h1>Kzarka</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Kutum" title="Kutum" coords="1178,409,44" shape="circle"/>} modal>
                    <h1>Kutum</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Nouver" title="Nouver" coords="1326,473,45" shape="circle"/>} modal>
                    <h1>Nouver</h1>
                </Popup>
                <Popup trigger={<area target="" alt="Vell" title="Vell" coords="681,44,42" shape="circle"/>} modal>
                    <h1>Vell</h1>
                </Popup>
            </map>
            <img className={state.checked ? 'mapa_fullscreen' : 'mapa'} src='images/bosses/mapa.jpg' alt='Mapa' useMap="#image-map" onClick={handleChange}/>
            </div>
        </div>
        </>
    )
}

export default Bosses
