import React from 'react'
import { Link } from 'react-router-dom';
import './Intercambio.css'

function Intercambio() {

    return (
        <>
        <div className='container_navegacion' id='inicionavegacion'>
            <div className='container_block_right'>
                <img className='img_navegacion_info' src='../images/oceano/intercambio/barco.jpg' alt='Intercambio_1' />
                <div className='container_text'>
                    <h2 className='subtitulo_navegacion'>¿Como ganar plata facil?<br/>Intercambios maritimos!</h2>
                    <p className='texto_navegacion'>
                    Este documento te ayudara a saber todo sobre los intercambios maritimos, es una lifeskill que se puede hacer semi-afk, y dependiendo de cuantos intercambios
                    hagas podes llegar a hacer hasta 300 millones por dia. Entre mas hagas mas plata ganas!
                    </p>
                    <a className='link_navegacion' href='https://grumpygreen.cricket/bdo-barter-sailing/'>INTERCAMBIOS MARITIMOS</a>
                </div>
            </div>
            <div className='container_block_left'>
                <img className='img_navegacion_info' src='../images/oceano/intercambio/barco_2.jpg' alt='Intercambio_2' />
                <div className='container_text'>
                    <h2 className='subtitulo_navegacion'>Recomendacion:</h2>
                    <p className='texto_navegacion'>
                    Para hacer trades maritimos vas a necesitar un barco, y talvez necesites mejorar el que tengas. Por eso recomiendo ver la seccion de NAVEGACION, donde vas a poder
                    ver como mejorar tu barco
                    </p>
                    <Link className='link_navegacion' to='/oceano/Navegacion'>NAVEGACION</Link>
                </div>
            </div>
        </div>
        </>
    )
}

export default Intercambio